import React, { Component } from 'react'
import $ from 'jquery'

import JWT from '../helpers/jwt_helper'

export default class LoginForm extends Component{
  constructor(){
    super();
    this.state = { output: '' }
  }
  getEmailValue(ref){
    this.email = ref;
  }
  getPasswordValue(ref){
    this.password = ref;
  }
  handleSubmit(e){
    e.preventDefault();
    let email = this.email.value;
    let password = this.password.value;

    if(!password || !email){
      this.setState({ output: "Fill'em Both Out Yo."});
      return;
    }
    
    const admin = {
      email, password
    };
    
    $.ajax({
      url: "/api/admin/login",
      type: "POST",
      data: admin
    })
    .success(res =>{
      JWT.save(res.token);
      this.context.router.replace('/clients');
      console.log('Success!');
      console.dir(res.token);
    })
    .error((jqXHR, status, error) =>{
      console.log(status);
      console.error(error);
    })

  }
  render(){
    return(
      <div className="jumbotron">
        
        <form className="login-form" onSubmit={this.handleSubmit.bind(this)}>
          <h4>Login</h4>
          <fieldset className="form-group">
            <input 
              type="text" 
              id="admin-email" 
              name="admin-email" 
              defaultValue="mickeyj.music@gmail.com"
              className="form-control" 
              ref={(ref) => this.getEmailValue(ref)}
            />
          </fieldset>
          <fieldset className="form-group">
            <input
              type="password"
              id="admin-password"
              name="admin-password"
              defaultValue="asdfzxcv"
              className="form-control"
              ref={(ref) => this.getPasswordValue(ref)}
            />
          </fieldset>
          <input className="btn btn-info" type="submit" value="Login"/>
        </form>

        <span className="submit-message">{this.state.output}</span>

      </div>
    )
  }
}

LoginForm.contextTypes = {
  router: React.PropTypes.object
};


