import {
  LOG_IN,
  IS_AUTHED,
} from './actions'

const INITIAL_STATE = { cred: {}, token: '', error: {} };

export default function(state = INITIAL_STATE, action) {
  switch(action.type){
    case LOG_IN:
      return { ...state,
        cred: action.payload.data.admin,
        token: action.payload.data.token
      };
    case IS_AUTHED:
      return { ...state,
        ...state.token,
        cred: action.payload.data.admin,
        error: action.payload.data.message
      };
    default:
      return state
  }
}


