import React from 'react'
import { Link } from 'react-router'

import NavLink from './../components/NavLink'

const Navbar = (props) => {
  if(!props.token){
    return(
      <nav>
        <span>login</span>
      </nav>
    )
  } else {
    return (
      <nav>
        <NavLink to="/clients">
          <span>Clients</span>
        </NavLink>
        <i> | </i>
        <Link to="/login" onClick={props.handleLogout.bind(this)}>Logout</Link>
      </nav>
    )
  }
};

export default Navbar


